import { NextRequest, NextResponse } from 'next/server'
import { createClient } from '@/lib/supabase/server'
import { electronicAttendanceService } from '@/lib/services/electronic-attendance.service'

/**
 * GET /api/electronic-attendance/sessions/[id]
 * R√©cup√®re une session d'√©margement par son ID
 */
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ id: string }> }
) {
  try {
    const { id } = await params
    const supabase = await createClient()
    const { data: { user }, error: authError } = await supabase.auth.getUser()

    if (authError || !user) {
      return NextResponse.json(
        { error: 'Non authentifi√©' },
        { status: 401 }
      )
    }

    const session = await electronicAttendanceService.getAttendanceSessionById(id)

    return NextResponse.json(session)
  } catch (error) {
    console.error('Erreur lors de la r√©cup√©ration de la session d\'√©margement:', error)
    return NextResponse.json(
      { error: error instanceof Error ? error.message : 'Erreur serveur' },
      { status: 500 }
    )
  }
}

/**
 * PATCH /api/electronic-attendance/sessions/[id]
 * Met √† jour une session d'√©margement (lancer, fermer, etc.)
 */
export async function PATCH(
  request: NextRequest,
  { params }: { params: Promise<{ id: string }> }
) {
  try {
    const { id } = await params
    const supabase = await createClient()
    const { data: { user }, error: authError } = await supabase.auth.getUser()

    if (authError || !user) {
      return NextResponse.json(
        { error: 'Non authentifi√©' },
        { status: 401 }
      )
    }

    const body = await request.json()
    const { action, sendEmails } = body

    if (action === 'launch') {
      const result = await electronicAttendanceService.launchAttendanceSession(
        id,
        sendEmails !== false
      )
      return NextResponse.json(result)
    }

    if (action === 'close') {
      const result = await electronicAttendanceService.closeAttendanceSession(id)
      return NextResponse.json(result)
    }

    return NextResponse.json(
      { error: 'Action non reconnue' },
      { status: 400 }
    )
  } catch (error) {
    console.error('Erreur lors de la mise √† jour de la session d\'√©margement:', error)
    return NextResponse.json(
      { error: error instanceof Error ? error.message : 'Erreur serveur' },
      { status: 500 }
    )
  }
}
